
import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomCenter } from './customCenter.component';
const routes: Routes = [
    {
        path: '',
        component: CustomCenter,
        children: [
            {path:'element-combination',loadChildren:'./element-combination/element-combination.module#ElementCombinationModule'},//要素组合
            {path:'customer-distribution',loadChildren:'./customer-distribution/customer-distribution.module#CustomerDistributionModule'},//客户分配
            {path:'credit-cust-allot',loadChildren:'./credit-cust-allot/credit-cust.module#CreditCustAllotTModule'},//信用卡直销-客户分配
            {path:'project-main-manage',loadChildren:'./project-main-manage/project-main-manage.module#ProjectMainManageModule'},//供应链协议
            //{path:'project-main-manage-qita',loadChildren:'./project-main-manage/project-main-manage.module#ProjectMainManageModule'},//其他合作项目
        ]
    }
];
export const CustomCenterRouting: ModuleWithProviders = RouterModule.forChild(routes);
